@charset "utf-8";

ul, li{padding:0; margin:0;}
a{
  text-decoration: none;
  color: #333;
}

.header_main_wrapper{
  display: flex;
  flex: 1;
}

header{
  width: 200px;
  background: #eee;
}
header h1{
  text-align: center;
  padding: 50px 0px;
  margin:0;
}
header nav{
  width: 100px;
  margin:0 auto;
}
header nav ul{
  list-style: none;
}
header nav ul li{
  margin-bottom: 10px;
}
main{
  width: 100%;
  background: #eee;
  display: grid;
  grid-template-columns: repeat(2, auto);
  overflow: hidden;
}
section{
  width: 100%;
  background: #eee;
}

.movie_info{
  margin: 50px;
  background: #fff;
}
.movie_info a{
  display: flex;
}
.movie_info img{
  box-shadow: 3px 5px 10px #888;
  margin: -10px 40px 20px 20px;
  transition: 0.5s;
  transform-style: preserve-3d;
}
.movie_info img:hover{
  transform: perspective(800px) rotateX(30deg);
}
.movie_info p{
  padding-right: 20px;
  display: -webkit-box;
  line-clamp: 6;
  -webkit-line-clamp: 6;
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.movie_info .text_box{
  min-width: 300px;
}


footer{
  background: #333;
  text-align: center;
}
footer address{
  line-height: 50px;
  color: #fff;
  font-style: normal;
}

.loading_img{
  width: 50px; height: 50px;
  margin: 100px;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  object-fit: cover;
  position: fixed;
  z-index: 10000;
}

@media screen and (max-width:1024px) {
  main{
    grid-template-columns: repeat(1, auto);
  }
  .header_main_wrapper{
    display: block;
  }
  header nav{
    display:none;
  }
  header{
    width: 100%;
  }
  .movie_info{
    margin: 30px 0px;
  }
}